<template>
  <div
    style="padding-top: 100px"
    :style="`background-color: ${
      model ? ' hsl(0, 0%, 98%)' : 'hsl(207, 26%, 17%)'
    };`"
  >
    <div
      style="
        top: 50%;
        left: 50%;
        transform: translate(-50%);
        position: absolute;
      "
      v-if="loading"
    >
      <v-progress-circular
        model-value="20"
        :size="128"
        :width="6"
        color="primary"
        indeterminate
      ></v-progress-circular>
    </div>
    <v-container
      v-if="!loading"
      :style="`color: ${
        model ? 'hsl(207, 26%, 17%)' : ' hsl(0, 0%, 98%)'
      } !important;`"
    >
      <v-btn
        style="margin-top: 20px"
        @click="$router.push({ name: 'home' })"
        :color="`${model ? 'white' : 'hsl(209, 23%, 22%)'}`"
        >Back</v-btn
      >
      <v-row class="mt-8 d-flex">
        <v-col lg="6" md="6" sm="12" cols="12"
          ><img
            :src="singleCountry.flags.svg"
            v-if="!loading"
            alt=""
            class="w-100"
            height="300"
        /></v-col>
        <v-col lg="6" md="6" sm="12" cols="12">
          <v-card
            elevation="0"
            :style="`color: ${
              model ? 'hsl(207, 26%, 17%)' : ' hsl(0, 0%, 98%)'
            } !important;background-color: transparent !important`"
            v-if="!loading"
          >
            <v-card-title
              class="px-0"
              style="font-weight: 600; font-size: 28px"
              v-if="!loading"
              >{{ singleCountry.name.common }}</v-card-title
            >
            <v-row>
              <v-col lg="6" md="6" sm="12" cols="12" class="mt-6"
                ><ul v-if="!loading">
                  <li
                    class="d-flex"
                    style="font-size: 16px; font-weight: 500"
                  >
                    Native Name :
                    <span
                      style="
                        font-size: 14px;
                        margin-left: 5px;
                        color: rgb(137 137 137);
                      "
                    >
                      {{ singleCountry.translations.fra.common }}</span
                    >
                  </li>
                  <li
                    class="d-flex"
                    style="font-size: 16px; font-weight: 500"
                  >
                    populatio :
                    <span
                      style="
                        font-size: 14px;
                        margin-left: 5px;
                        color: rgb(137 137 137);
                      "
                    >
                      {{ singleCountry.population }}</span
                    >
                  </li>
                  <li
                    class="d-flex"
                    style="font-size: 16px; font-weight: 500"
                  >
                    region :
                    <span
                      style="
                        font-size: 14px;
                        margin-left: 5px;
                        color: rgb(137 137 137);
                      "
                    >
                      {{ singleCountry.region }}
                    </span>
                  </li>
                  <li
                    class="d-flex"
                    style="font-size: 16px; font-weight: 500"
                  >
                    sub region :
                    <span
                      style="
                        font-size: 14px;
                        margin-left: 5px;
                        color: rgb(137 137 137);
                      "
                    >
                      {{ singleCountry.subregion }}</span
                    >
                  </li>
                  <li
                    class="d-flex"
                    style="font-size: 16px; font-weight: 500"
                  >
                    capital :
                    <span
                      style="
                        font-size: 14px;
                        margin-left: 5px;
                        color: rgb(137 137 137);
                      "
                    >
                      {{
                        `${singleCountry.capital}`.toString("") == "undefined"
                          ? "No Capital"
                          : `${singleCountry.capital}`.toString("")
                      }}</span
                    >
                  </li>
                </ul></v-col
              >
              <v-col lg="6" md="6" sm="12" cols="12" class="mt-6">
                <ul v-if="!loading">
                  <li
                    class="d-flex"
                    style="font-size: 16px; font-weight: 500"
                  >
                    Top Level Domain:
                    <span
                      style="
                        font-size: 14px;
                        margin-left: 5px;
                        color: rgb(137 137 137);
                      "
                    >
                      {{ `${singleCountry.cca2}`.toLowerCase() }}</span
                    >
                  </li>
                  <li
                    class="d-flex"
                    style="font-size: 16px; font-weight: 500"
                  >
                    Currencies :
                    <span
                      style="
                        font-size: 14px;
                        margin-left: 5px;
                        color: rgb(137 137 137);
                      "
                    >
                      {{ Object.keys(this.singleCountry.currencies)[0] }}</span
                    >
                  </li>
                  <li
                    style="font-size: 16px; font-weight: 500"
                  >
                    Languages :
                    <span
                      v-for="lang in singleCountry.languages"
                      :key="lang"
                      style="
                        font-size: 14px;
                        margin-left: 10px;
                        color: rgb(137 137 137);
                      "
                    >
                      {{ lang }}
                    </span>
                  </li>
                </ul>
              </v-col>
            </v-row>
          </v-card>
          <v-card
            elevation="0"
            :style="`color: ${
              model ? 'hsl(207, 26%, 17%)' : ' hsl(0, 0%, 98%)'
            } !important;background-color: transparent !important`"
            class="px-0 mt-6"
            v-if="!loading"
          >
            <v-card-title
              v-if="singleCountry.borders"
              style="
                white-space: pre-wrap !important;
                font-weight: 500;
                font-size: 18px;
              "
              class="px-0"
              >Border Countries :<br />
              <span
                v-for="border in singleCountry.borders"
                :key="border"
                style="margin-left: 5px"
              >
                <v-chip
                  label
                  class="mt-3"
                  @click="
                    $router.push({
                      name: 'country',
                      params: { name: border },
                    }),
                      getSingleCountry(border)
                  "
                >
                  {{ border }}
                </v-chip></span
              ></v-card-title
            >
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import { useCountryStore } from "@/stores/country";
import { mapState, mapActions } from "pinia";
export default {
  data: () => ({
    loading: false,
  }),
  computed: {
    ...mapState(useCountryStore, ["model", "singleCountry"]),
  },
  methods: {
    ...mapActions(useCountryStore, ["getSingleCountry"]),
  },
  async beforeMount() {
    this.loading = true;
    await this.getSingleCountry(this.$route.params.name);
    console.log(this.singleCountry);
    this.loading = false;
  },
};
</script>
